<template>
  <svg class="icon">
    <use :xlink:href="'#'+name" class="use" />
  </svg>
</template>

<script>
let importAll = requireContext => {
  console.log(requireContext);
  requireContext.keys().forEach(requireContext);
};
try {
  importAll(require.context("./icons", true, /\.svg$/));
} catch (error) {
  console.log(error);
}
export default {
  name: "Icon",
  props: {
    name: {
      type: String
    }
  }
};
</script>

<style lang="scss" scoped>
.icon {
  width: 1em;
  height: 1em;
  vertical-align: -0.15em;
  fill: currentColor;
  overflow: hidden;
}
</style>